$color_white: #fff;
$color_prime: #0d3da5;
$color_grey: #e2e2e2;
$color_grey_dark: #a2a2a2;

.banner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--background-color);
  padding: 8px;
}

@media (max-width: 1120px) {
  .banner-container {
    flex-direction: column;
  }
}

.banner {
  width: -webkit-fill-available;
  height: 350px;
  opacity: 0.3;
  z-index: 0;
}

.overlay-image {
  z-index: 1;
  width: 200px;
  height: auto;
  border-radius: 50%;
  border: 5px solid #ffffff;
}

.blog-card {
  display: flex;
  flex-direction: column;
  margin: 1rem auto;
  box-shadow: 0 3px 7px 5px rgba(#000, .1);
  margin-bottom: 1.6%;
  background: $color_white;
  line-height: 1.4;
  font-family: sans-serif;
  border-radius: 5px;
  overflow: hidden;
  z-index: 0;

  a {
    color: inherit;

    &:hover {
      color: $color_prime;
    }
  }

  &:hover {
    .photo {
      transform: scale(1.3) rotate(3deg);
    }
  }

  .meta {
    position: relative;
    z-index: 0;
    height: 200px;
  }

  .photo {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
    background-position: center;
    transition: transform .2s;
  }

  .details,
  .details ul {
    margin: auto;
    padding: 0;
    list-style: none;
  }

  .details {
    position: absolute;
    top: 0;
    bottom: 0;
    left: -100%;
    margin: auto;
    transition: left .2s;
    background: rgba(#000, .6);
    color: $color_white;
    padding: 10px;
    width: 100%;
    font-size: .9rem;

    a {
      text-decoration: dotted underline
    }

    ul li {
      display: inline-block;
    }

    .author:before {
      font-family: FontAwesome;
      margin-right: 10px;
      content: "\f007";
    }

    .date:before {
      font-family: FontAwesome;
      margin-right: 10px;
      content: "\f133";
    }

    .tags {
      ul:before {
        font-family: FontAwesome;
        content: "\f02b";
        margin-right: 10px;
      }

      li {
        margin-right: 2px;

        &:first-child {
          margin-left: -4px;
        }
      }
    }
  }

  .description {
    padding: 1rem;
    background: $color_white;
    position: relative;
    z-index: 1;

    h2 {
      font-family: Poppins, sans-serif;
    }

    h2 {
      font-size: 1rem;
      font-weight: 300;
      text-transform: uppercase;
      color: black;
      margin-top: 5px;
    }

    .read-more {
      text-align: right;
      color: $color_prime;
      display: inline-block;
      position: relative;

      &:after {
        content: "\f061";
        font-family: FontAwesome;
        margin-left: -10px;
        opacity: 0;
        vertical-align: middle;
        transition: margin .3s, opacity .3s;
      }

      &:hover:after {
        margin-left: 5px;
        opacity: 1;
      }
    }
  }

  p {
    position: relative;
    margin: 1rem 0 0;
    color: $color_grey_dark;

    &:first-of-type {
      margin-top: 1.25rem;

      &:before {
        content: "";
        position: absolute;
        height: 5px;
        background: $color_prime;
        width: 35px;
        top: -0.75rem;
        border-radius: 3px;
      }
    }
  }

  &:hover {
    .details {
      left: 0%;
    }
  }


  @media (min-width: 640px) {
    flex-direction: row;
    .meta {
      flex-basis: 40%;
      height: auto;
    }
    .description {
      flex-basis: 60%;

      &:before {
        transform: skewX(-3deg);
        content: "";
        background: #fff;
        width: 30px;
        position: absolute;
        left: -10px;
        top: 0;
        bottom: 0;
        z-index: -1;
      }
    }
    &.alt {
      flex-direction: row-reverse;

      .description {
        &:before {
          left: inherit;
          right: -10px;
          transform: skew(3deg)
        }
      }

      .details {
        padding-left: 25px;
      }
    }
  }
}

.dialog-centered {
  width: 600px;
  height: 300px;
  overflow: auto;
}
